import "core-js/modules/es.array.push.js";
import { defineComponent } from "vue";
import router from "@/router";
export default defineComponent({
  setup() {
    const nav = path => {
      router.push({
        path: path
      });
    };
    const commandHandle = command => {
      router.push({
        path: command
      });
    };
    return {
      nav,
      commandHandle
    };
  }
});